import React from 'react';
import PropTypes from 'prop-types';
import Header from '../components/Header';
import getMusics from '../services/musicsAPI';
import MusicCard from '../components/MusicCard';
import '../styles/Album.css';

class Album extends React.Component {
  constructor() {
    super();

    this.state = {
      musics: [],
    };
  }

  componentDidMount() {
    this.getMusicsFromAlbum();
  }

  getMusicsFromAlbum = async () => { // Engloba, entre outras coisas, a fun√ß√£o ass√≠ncrona getMusics(). Essa √∫ltima, realiza uma requisi√ß√£o para a API do Itunes que retorna um array de objetos onde, cada objeto, diz respeito √† uma m√∫sica de um determinado √°lbum.
    const { match: { params: { id } } } = this.props; // O id √© uma informa√ß√£o acumulada pelo pr√≥prio <Route /> que encapsula o componente React de classe "Album".

    const response = await getMusics(id);

    this.setState({ // O primeiro elemento do array (√≠ndice 0), oriundo da response, acumula informa√ß√µes do album e n√£o de uma m√∫sica espec√≠fica, como ocorre nos elementos de √≠ndice >= 1.
      musics: response,
      albumImg: response[0].artworkUrl100,
      albumName: response[0].collectionName,
      artist: response[0].artistName,
    });
  }

  render() {
    const { musics, albumImg, albumName, artist } = this.state;
    return (
      <div id="page-album" data-testid="page-album">
        <Header />
        <main id="albumPageMain">
          <section id="albumInfoContainer">
            <img src={ albumImg } alt={ albumName } />
            <h4 data-testid="album-name">{ albumName }</h4>
            <p data-testid="artist-name">{ artist }</p>
          </section>
          <section id="tracksListContainer">
            {musics.map((microObj, index) => (
              index > 0 && <MusicCard trackInfo={ microObj } key={ microObj.trackId } />
              // O primeiro elemento do array (√≠ndice 0) acumula informa√ß√µes do album e n√£o de uma m√∫sica espec√≠fica.
            ))}
          </section>
        </main>
      </div>
    );
  }
}

Album.propTypes = {
  match: PropTypes.shape({
    params: PropTypes.shape({
      id: PropTypes.string,
    }),
  }).isRequired,
};

export default Album;
